	module.exports = {"data":{"meImage":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":1.0256410256410255,"src":"/static/me-cut-small-734f68ab3adb6455e4aeeb3ba3189708-20c2d.png","srcSet":"/static/me-cut-small-734f68ab3adb6455e4aeeb3ba3189708-4e92c.png 75w,\n/static/me-cut-small-734f68ab3adb6455e4aeeb3ba3189708-93059.png 150w,\n/static/me-cut-small-734f68ab3adb6455e4aeeb3ba3189708-20c2d.png 200w","sizes":"(max-width: 200px) 100vw, 200px"}},"meImageFull":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":0.821608040201005,"src":"/static/me-full-4690adb5e52d59c5c7db505c8308872d-7c9e1.png","srcSet":"/static/me-full-4690adb5e52d59c5c7db505c8308872d-68f83.png 75w,\n/static/me-full-4690adb5e52d59c5c7db505c8308872d-24538.png 150w,\n/static/me-full-4690adb5e52d59c5c7db505c8308872d-7c9e1.png 300w,\n/static/me-full-4690adb5e52d59c5c7db505c8308872d-fd33d.png 327w","sizes":"(max-width: 300px) 100vw, 300px"}},"site":{"siteMetadata":{"url":"https://mi3o.com"}},"recentPosts":{"edges":[{"node":{"excerpt":"As I wrote in my previous post ( How GraphQL beats REST when I am hungry ) GraphQL has only one endpoint and that means our security configuration can…","frontmatter":{"title":"Secure your GraphQL with Spring AOP","date":"November 05, 2018","path":"/spring-graphql-security","type":"blog"}}},{"node":{"excerpt":"OK, so there we have some Servlet container, some EJB container, Spring container, this container, that container... Cool, everyone uses container…","frontmatter":{"title":"Throwing a party without a container","date":"August 27, 2018","path":"/throwing-a-party","type":"blog"}}},{"node":{"excerpt":"REST web services proved to be solid standard for communication and became easy way to expose server interfaces to clients. To introduce them into…","frontmatter":{"title":"How GraphQL beats REST when I am hungry","date":"August 16, 2018","path":"/how-graphql-beats-rest","type":"blog"}}},{"node":{"excerpt":"All courses have either fixed predefined agenda or can have individual scope depending on your specific needs. They can be tailored for beginners…","frontmatter":{"title":"Courses","date":"February 10, 2018","path":"/courses","type":"page"}}}]},"markdownRemark":{"html":"<p>OK, so there we have some Servlet container, some EJB container, Spring container, this container, that container... Cool, everyone uses container, but why? Can't we just make class instances by ourselves? To prove it I decided to make a party and I'll do it in an old fashioned way without any help of container.</p>\n<h2>Let's throw a party</h2>\n<p>I will focus just on one person, as to make the example simple. Meet my friend, ordinary party goer, Bob:</p>\n<div class=\"gatsby-highlight\" data-language=\"java\">\n      <pre class=\"language-java\"><code class=\"language-java\"><span class=\"token keyword\">class</span> <span class=\"token class-name\">Bob</span> <span class=\"token keyword\">implements</span> <span class=\"token class-name\">InvitedBuddy</span> <span class=\"token punctuation\">{</span>\n\n  <span class=\"token annotation punctuation\">@Override</span>\n  <span class=\"token keyword\">public</span> <span class=\"token keyword\">void</span> <span class=\"token function\">partyTime</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    partyService<span class=\"token punctuation\">.</span><span class=\"token function\">joinParty</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"What's up guys?\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    snackService<span class=\"token punctuation\">.</span><span class=\"token function\">gimme</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"burger\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    barService<span class=\"token punctuation\">.</span><span class=\"token function\">canIHave</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"beer\"</span><span class=\"token punctuation\">,</span> <span class=\"token number\">1</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    barService<span class=\"token punctuation\">.</span><span class=\"token function\">canIHave</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"vodka\"</span><span class=\"token punctuation\">,</span> <span class=\"token number\">1</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    barService<span class=\"token punctuation\">.</span><span class=\"token function\">oneMorePlease</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n    socialService<span class=\"token punctuation\">.</span><span class=\"token function\">makeAJoke</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"A dyslexic man walks into a bra.\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    barService<span class=\"token punctuation\">.</span><span class=\"token function\">oneMorePlease</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token comment\">// beginning of the end</span>\n    socialService<span class=\"token punctuation\">.</span><span class=\"token function\">huntForGirls</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    barService<span class=\"token punctuation\">.</span><span class=\"token function\">canIHave</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"Whatever but fast\"</span><span class=\"token punctuation\">,</span> Integer<span class=\"token punctuation\">.</span>MAX_VALUE<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    socialService<span class=\"token punctuation\">.</span><span class=\"token function\">makeAJoke</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"...\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span> <span class=\"token comment\">// you really don't want to hear this one</span>\n\n    RuntimeException up <span class=\"token operator\">=</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">AlcoholOverflowException</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"blarghhhh\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token keyword\">throw</span> up<span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>Brief examination:</p>\n<ul>\n<li>Bob joins the party and says hello - so we'll need authentication.</li>\n<li>He drinks an alcohol. OK, it's legal for Bob but what about the others? - authorization added.</li>\n<li>Can't judge, but seems after several calls to barService there might be a case when something is thrown, e. g. en Exception - would be nice to involve transactions.</li>\n<li>And last but not least, I'd like to keep track of everything that is happening at the party - let's put logging in place</li>\n</ul>\n<h2>Security</h2>\n<p>We want to have private party therefore every service call must require fully authenticated and in case of barService also authorized users. Let's start with a very very simplified login mechanism:</p>\n<div class=\"gatsby-highlight\" data-language=\"java\">\n      <pre class=\"language-java\"><code class=\"language-java\"><span class=\"token keyword\">public</span> <span class=\"token keyword\">class</span> <span class=\"token class-name\">PartyService</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token comment\">// imagine constructor and other stuff here..</span>\n\n  <span class=\"token comment\">// login to party</span>\n  <span class=\"token keyword\">public</span> String <span class=\"token function\">joinParty</span><span class=\"token punctuation\">(</span>String hello<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token comment\">// try to recognize who is talking to you</span>\n    Friend friend <span class=\"token operator\">=</span> friendRepository<span class=\"token punctuation\">.</span><span class=\"token function\">findByVoiceAndFace</span><span class=\"token punctuation\">(</span>hello<span class=\"token punctuation\">,</span> faceScan<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n    <span class=\"token comment\">// unknown people shell not pass</span>\n    <span class=\"token keyword\">if</span> <span class=\"token punctuation\">(</span>friend <span class=\"token operator\">==</span> null<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n      <span class=\"token keyword\">throw</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">HeyManWhatAreYouDoingHereException</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token punctuation\">}</span>\n\n    <span class=\"token comment\">// generate and remember unique ID for your friend</span>\n    String partyId <span class=\"token operator\">=</span> UUID<span class=\"token punctuation\">.</span><span class=\"token function\">randomUUID</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    partyMap<span class=\"token punctuation\">.</span><span class=\"token function\">put</span><span class=\"token punctuation\">(</span>partyId<span class=\"token punctuation\">,</span> friend<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n    <span class=\"token comment\">// pass your friend ticket he will use to</span>\n    <span class=\"token comment\">// call service methods</span>\n    <span class=\"token keyword\">return</span> partyId<span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n\n  <span class=\"token comment\">// Here is how to check if someone is allowed to be at party</span>\n  <span class=\"token keyword\">public</span> Friend <span class=\"token function\">whoIsThisGuy</span><span class=\"token punctuation\">(</span>String partyId<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    Friend friend <span class=\"token operator\">=</span> partyMap<span class=\"token punctuation\">.</span><span class=\"token function\">get</span><span class=\"token punctuation\">(</span>partyId<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token keyword\">if</span> <span class=\"token punctuation\">(</span>friend <span class=\"token operator\">==</span> null<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n      <span class=\"token keyword\">throw</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">HeyManWhatAreYouDoingHereException</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token punctuation\">}</span>\n\n    <span class=\"token keyword\">return</span> friend<span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>Now we have the authentication for people joining our party. Everyone who comes will receive a ticket to avoid unnecessary re-authentication and will show it to stuff when he visits the snack or bar service:</p>\n<div class=\"gatsby-highlight\" data-language=\"java\">\n      <pre class=\"language-java\"><code class=\"language-java\"><span class=\"token keyword\">public</span> <span class=\"token keyword\">class</span> <span class=\"token class-name\">BarService</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token comment\">// ... omitted lines of no interest</span>\n\n  <span class=\"token keyword\">public</span> Beverage <span class=\"token function\">canIHave</span><span class=\"token punctuation\">(</span>String partyId<span class=\"token punctuation\">,</span> String type<span class=\"token punctuation\">,</span> <span class=\"token keyword\">int</span> amount<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token comment\">// Check if he is who ho he pretends to be</span>\n    partyService<span class=\"token punctuation\">.</span><span class=\"token function\">whoIsThisGuy</span><span class=\"token punctuation\">(</span>partyId<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n    <span class=\"token comment\">// OK, no Exception was thrown - verified, handle his request</span>\n    <span class=\"token comment\">// ...</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token keyword\">public</span> <span class=\"token keyword\">class</span> <span class=\"token class-name\">SnackService</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token comment\">// ... omitted lines of no interest</span>\n\n  <span class=\"token keyword\">public</span> Snack <span class=\"token function\">gimme</span><span class=\"token punctuation\">(</span>String partyId<span class=\"token punctuation\">,</span> String type<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token comment\">// Check if he is who ho he pretends to be</span>\n    partyService<span class=\"token punctuation\">.</span><span class=\"token function\">whoIsThisGuy</span><span class=\"token punctuation\">(</span>partyId<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n    <span class=\"token comment\">// OK, no Exception was thrown - verified, handle his request</span>\n    <span class=\"token comment\">// ...</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>Did you spot the differences in the services' methods? No? That's right since there is not any. Every service method in our party:</p>\n<ul>\n<li>must have <em>partyId</em> argument</li>\n<li>must verify the caller before the request is handled</li>\n</ul>\n<p>What we got here is code duplication and certainly code pollution - imagine we plan to have also music service, game service, darts service... What we want here is to get rid of boilerplate code and focus only on our party business.</p>\n<p>Our little security verification code spreads all over our application and in programming terminology it would be called an <em>aspect</em>. And how to deal with aspects? Well, these are best addressed by Aspect-Oriented Programming (AOP). In a nutshell, to organize such scattered code you need to create a rule which says in which classes and methods you want to call your aspect. That way you can fully remove your repetitive code lines and have it just in one place.</p>\n<p>Warning, spoiler follows: All of the points I put down at the beginning (security, transaction, logging) are very good candidates for AOP and would be solved in very similar way as I am going to deal with authentication. Therefore, for the sake of you precious time, those will not be covered in my post.</p>\n<p>But let's go step by step.</p>\n<h2>Forget tickets, use wristbands</h2>\n<p>We don't want people at our party to care about tickets whenever they need something. That would spoil the fun certainly. I will store the tickets on them - I will use wristbands. Can you think of any Java wristband implementation?</p>\n<p><a href=\"https://docs.oracle.com/javase/8/docs/api/java/lang/ThreadLocal.html\">ThreadLocal</a> is a storage which is unique for each thread that accesses it. I know that everyone at my party uses just one thread, we are having a standard <em>Servlet</em> party, therefore I will use <em>Threads</em> to store the party identification:</p>\n<div class=\"gatsby-highlight\" data-language=\"java\">\n      <pre class=\"language-java\"><code class=\"language-java\"><span class=\"token keyword\">public</span> <span class=\"token keyword\">class</span> <span class=\"token class-name\">PartyService</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">public</span> <span class=\"token keyword\">void</span> <span class=\"token function\">joinParty</span><span class=\"token punctuation\">(</span>String hello<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token comment\">// same good old code lines here as before</span>\n\n    <span class=\"token comment\">// NOT NEEDED ANYMORE:</span>\n    <span class=\"token comment\">// String partyId = UUID.randomUUID();</span>\n    <span class=\"token comment\">// sessionMap.put(partyId, friend);</span>\n\n    <span class=\"token comment\">// every guest has its own body (thread)</span>\n    <span class=\"token comment\">// for the wristband ticket;</span>\n    partyThreadLocal<span class=\"token punctuation\">.</span><span class=\"token function\">set</span><span class=\"token punctuation\">(</span>friend<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n    <span class=\"token comment\">// no need to return anything from this method</span>\n  <span class=\"token punctuation\">}</span>\n\n  <span class=\"token comment\">// we don't need partyId argument here anymore</span>\n  <span class=\"token keyword\">public</span> <span class=\"token keyword\">void</span> <span class=\"token function\">whoIsThisGuy</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    Friend friend <span class=\"token operator\">=</span> partyThreadLocal<span class=\"token punctuation\">.</span><span class=\"token function\">get</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token keyword\">if</span> <span class=\"token punctuation\">(</span>friend <span class=\"token operator\">==</span> null<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n      <span class=\"token keyword\">throw</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">HeyManWhatAreYouDoingHereException</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token punctuation\">}</span>\n\n    <span class=\"token keyword\">return</span> friend<span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>Fine, we are half way there. Now we don't need the <em>partyId</em> argument, but we still need to call the verification in all services and theirs methods:</p>\n<div class=\"gatsby-highlight\" data-language=\"java\">\n      <pre class=\"language-java\"><code class=\"language-java\"><span class=\"token keyword\">public</span> <span class=\"token keyword\">class</span> <span class=\"token class-name\">BarService</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token comment\">// ... omitted lines of no interest for now</span>\n\n  <span class=\"token keyword\">public</span> Beverage <span class=\"token function\">canIHave</span><span class=\"token punctuation\">(</span>String type<span class=\"token punctuation\">,</span> <span class=\"token keyword\">int</span> amount<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token comment\">// the caller will be identified by the thread he is using</span>\n    partyService<span class=\"token punctuation\">.</span><span class=\"token function\">whoIsThisGuy</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n    <span class=\"token comment\">// ... actual method implementation</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token comment\">// the same applies to other services...</span></code></pre>\n      </div>\n<h2>Step two, method call removal</h2>\n<p>I don't want other services to do the party people verification. They might forget it, or do it wrong.\nInstead, I will do it myself. And how? What about wrapping services in a proxy and intercept every action they do:</p>\n<div class=\"gatsby-highlight\" data-language=\"java\">\n      <pre class=\"language-java\"><code class=\"language-java\">  <span class=\"token comment\">// create an instance of service to be wrapped</span>\n  IBarService target <span class=\"token operator\">=</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">BarService</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n  <span class=\"token comment\">// create a proxy</span>\n  IBarService barService <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span>IBarService<span class=\"token punctuation\">)</span> Proxy<span class=\"token punctuation\">.</span><span class=\"token function\">newProxyInstance</span><span class=\"token punctuation\">(</span>\n      BarService<span class=\"token punctuation\">.</span><span class=\"token keyword\">class</span><span class=\"token punctuation\">.</span><span class=\"token function\">getClassLoader</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">,</span>\n      <span class=\"token comment\">// Proxy from JDK API can work only with classes that implement interface(s)</span>\n      <span class=\"token keyword\">new</span> <span class=\"token class-name\">Class</span><span class=\"token punctuation\">[</span><span class=\"token punctuation\">]</span> <span class=\"token punctuation\">{</span> IBarService<span class=\"token punctuation\">.</span><span class=\"token keyword\">class</span> <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n      <span class=\"token punctuation\">(</span>proxy<span class=\"token punctuation\">,</span> method<span class=\"token punctuation\">,</span> arguments<span class=\"token punctuation\">)</span> <span class=\"token operator\">-</span><span class=\"token operator\">></span> <span class=\"token punctuation\">{</span>\n          <span class=\"token comment\">// before every service method call verification is triggered</span>\n          partyService<span class=\"token punctuation\">.</span><span class=\"token function\">whoIsThisGuy</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n          <span class=\"token comment\">// call the original method</span>\n          <span class=\"token keyword\">return</span> method<span class=\"token punctuation\">.</span><span class=\"token function\">invoke</span><span class=\"token punctuation\">(</span>target<span class=\"token punctuation\">,</span> arguments<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n      <span class=\"token punctuation\">}</span>\n  <span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span></code></pre>\n      </div>\n<p>Ta-da! Using this approach we can get rid of all authentication logic from service classes and they can solely focus on their own business:</p>\n<div class=\"gatsby-highlight\" data-language=\"java\">\n      <pre class=\"language-java\"><code class=\"language-java\"><span class=\"token keyword\">public</span> <span class=\"token keyword\">class</span> <span class=\"token class-name\">BarService</span> <span class=\"token keyword\">implements</span> <span class=\"token class-name\">IBarService</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token comment\">// ... other methods here</span>\n\n  <span class=\"token annotation punctuation\">@Override</span>\n  <span class=\"token keyword\">public</span> Beverage <span class=\"token function\">canIHave</span><span class=\"token punctuation\">(</span>String type<span class=\"token punctuation\">,</span> <span class=\"token keyword\">int</span> amount<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token comment\">// this method does not even care about verification</span>\n    <span class=\"token comment\">// it is done by me</span>\n\n    <span class=\"token comment\">// method implementation</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token comment\">// the same applies to other services...</span></code></pre>\n      </div>\n<p>What we achieved here is that if we apply similar logic to all other issues on our list (authorization, transactions, logging) we will get very clean code that is much easier to read, develop, maintain and test and is not tangled with repetitive code that relates mainly to infrastructure.</p>\n<h2>Conclusion</h2>\n<p>And now is the right time for a moment of a reflection. How did the code transform?</p>\n<p>For my solution to be feasible</p>\n<ul>\n<li>I need to be the one who constructs services' instances</li>\n<li>I intercept and manage code flow of the services</li>\n</ul>\n<p>Which means I am now the one who fully controls the party. More precisely, what happened here is an Inversion of Control, so called IoC or someone might call it <em>Container</em> pattern. And the bottom line is, I reinvented the wheel.</p>\n<p>Container is not imaginary black box that just does something. It's a real code that was created for you to make your life as a programmer easier. Had I started from the beginning with Spring and used its capabilities I would have my whole party done by now.</p>\n<br />\n<p><em>The story, all names, characters, and incidents portrayed in this production are fictitious. No identification with actual persons is intended or should be inferred. Especially, barService() has nothing to do with famous foo() and bar() actors.</em></p>","excerpt":"OK, so there we have some Servlet container, some EJB container, Spring container, this container…","frontmatter":{"date":"August 27, 2018","path":"/throwing-a-party","title":"Throwing a party without a container","label":["container","java","Spring","JEE","ThreadLocal","security"],"author":"Michal Gebauer","type":"blog","image":{"publicURL":"/static/party-2ee6dd7b03be7aa3ccd3103a9f93a2a7.png","childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":4.413145539906103,"src":"/static/party-2ee6dd7b03be7aa3ccd3103a9f93a2a7-ab24c.png","srcSet":"/static/party-2ee6dd7b03be7aa3ccd3103a9f93a2a7-310d9.png 310w,\n/static/party-2ee6dd7b03be7aa3ccd3103a9f93a2a7-63c25.png 620w,\n/static/party-2ee6dd7b03be7aa3ccd3103a9f93a2a7-ab24c.png 1240w,\n/static/party-2ee6dd7b03be7aa3ccd3103a9f93a2a7-80007.png 1860w,\n/static/party-2ee6dd7b03be7aa3ccd3103a9f93a2a7-9d462.png 1880w","sizes":"(max-width: 1240px) 100vw, 1240px"}}}}}},"pathContext":{}}

